import React, { useEffect } from 'react';
// import * as _ from 'underscore'
import * as _ from 'lodash'

import theme from "../../theme";
import useCore from "../../hooks/useCore";

import DataField from "../DataField";
import { ITokenSymbolProp } from "../../utils/interface";

const forwardFixedSwapProj = ['FORWARDforwardFixedSwap', 'FORWARDforwardmahax2FixedSwap', 'FORWARDforwardmahaxFixedSwap', 'FORWARDforwardsquareupFixedSwap']
const sclpFixedSwapProj = ['SCLPscallopFixedSwap', 'SCLPscallopmahaxFixedSwap']

function HomeInvestorsCount(props: ITokenSymbolProp) {

  const core = useCore();
  const [arrayOfInvestors, setArrayOfInvestors] = React.useState<any>([])
  const [count, setCount] = React.useState<any>(0)

  const getInvestorsCount = () => {
    if(props.tokenSymbol === 'FORWARD'){
      forwardFixedSwapProj.map((proj) => {
        core.contracts[proj].getBuyers().then((res:any) => {
         setArrayOfInvestors((prev:any) => [...prev, res])
       })
     })
    }
    else{
      sclpFixedSwapProj.map((proj) => {
        core.contracts[proj].getBuyers().then((res:any) => {
         setArrayOfInvestors((prev:any) => [...prev, res])
         console.log('sclpFixedSwapProj', res);
       })
     })
    }
  }

  useEffect(() => {
    getInvestorsCount()
  },[])

  useEffect(() => {
    const result =  _.uniq(_.flatMap(arrayOfInvestors)).length
    setCount(result)
  },[arrayOfInvestors])

  console.log('test', count);


  return (
    <div className="m-b-16">
        <DataField
          label={"Investors"}
          isValueLoading={false}
          value={`${count}`}
          labelFontSize={18}
          labelFontColor={theme.color.transparent[100]}
          valueFontSize={18}
          valueFontColor={"white"}
          valueFontWeight={600}
        />
      </div>
  )
}

export default HomeInvestorsCount;
